import React, { useState, useEffect } from 'react';
import { useLocation } from 'react-router-dom';
import classNames from 'classnames';
import SearchForm from '../../components/SearchForm/SearchForm';
import styles from './Main.module.scss';
import iconMonitor from '../../images/icon-monitor.png';
import iconOffice from '../../images/icon-office.png';
import iconWorld from '../../images/icon-world.png';
import avatar1 from '../../images/avatar-1.png';
import avatar2 from '../../images/avatar-2.png';

const reviewsData = [
  {
    id: 1,
    name: ' ',
    text: '        .',
    img: avatar1,
  },
  {
    id: 2,
    name: ' ',
    text: '-  .    /     .',
    img: avatar2,
  },
  {
    id: 3,
    name: ' .',
    text: '  ,   ,    5 .',
    img: avatar1,
  },
  {
    id: 4,
    name: ' .',
    text: '  ,  ,     .',
    img: avatar2,
  },
  {
    id: 5,
    name: ' .',
    text: '  ,    .',
    img: avatar1,
  },
  {
    id: 6,
    name: ' .',
    text: ',       ,  .',
    img: avatar2,
  },
  {
    id: 7,
    name: ' .',
    text: '   ,  ,  .',
    img: avatar1,
  },
  {
    id: 8,
    name: ' .',
    text: ' ,    .',
    img: avatar2,
  },
  {
    id: 9,
    name: ' .',
    text: ', , .   .',
    img: avatar1,
  },
  {
    id: 10,
    name: ' .',
    text: '    ,  !',
    img: avatar2,
  },
];

export default function Main() {
  const [activeIndex, setActiveIndex] = useState(0);
  const location = useLocation();

  useEffect(() => {
    if (location.hash) {
      const id = location.hash.substring(1);
      const element = document.getElementById(id);

      if (element) {
        setTimeout(() => {
          element.scrollIntoView({ behavior: 'smooth', block: 'start' });
        }, 100);
      }
    }
  }, [location]);

  useEffect(() => {
    const interval = setInterval(() => {
      setActiveIndex((current) => (current === 4 ? 0 : current + 1));
    }, 10000);
    return () => clearInterval(interval);
  }, []);

  const currentReviews = reviewsData.slice(
    activeIndex * 2,
    activeIndex * 2 + 2
  );

  return (
    <main className={styles.mainPage}>
      <SearchForm />

      <section className={styles.aboutSection} id="about">
        <div className={styles.container}>
          <h2 className={styles.sectionTitle}> </h2>
          <div className={styles.aboutContent}>
            <p>
                 !      2003 . 14  
              ,          
               .
            </p>
            <p>
                      2
              ,     ?     
               .
            </p>
            <p style={{ fontWeight: 'bold' }}>
                    90    .
                      
               .
            </p>
          </div>
        </div>
      </section>

      <section className={styles.howItWorks} id="how-it-works">
        <div className={styles.container}>
          <div className={styles.featuresHeader}>
            <h2 className={styles.sectionTitleWhite}>  </h2>
            <button className={styles.ghostButton}> </button>
          </div>
          <div className={styles.featuresGrid}>
            <div className={styles.featureItem}>
              <div className={styles.iconCircle}>
                <img src={iconMonitor} alt="icon" />
              </div>
              <p>   </p>
            </div>
            <div className={styles.featureItem}>
              <div className={styles.iconCircle}>
                <img src={iconOffice} alt="icon" />
              </div>
              <p>    </p>
            </div>
            <div className={styles.featureItem}>
              <div className={styles.iconCircle}>
                <img src={iconWorld} alt="icon" />
              </div>
              <p>  </p>
            </div>
          </div>
        </div>
      </section>

      <section className={styles.reviews} id="reviews">
        <div className={styles.container}>
          <h2 className={styles.sectionTitle}></h2>

          <div className={styles.reviewCards}>
            {currentReviews.map((review) => (
              <div key={review.id} className={styles.reviewItem}>
                <img
                  src={review.img}
                  alt="avatar"
                  className={styles.reviewPhoto}
                />
                <div className={styles.reviewText}>
                  <h4>{review.name}</h4>
                  <p>{review.text}</p>
                </div>
              </div>
            ))}
          </div>

          <div className={styles.sliderDots}>
            {[0, 1, 2, 3, 4].map((index) => (
              <span
                key={index}
                className={classNames(styles.dot, {
                  [styles.activeDot]: index === activeIndex,
                })}
                onClick={() => setActiveIndex(index)}
              ></span>
            ))}
          </div>
        </div>
      </section>
    </main>
  );
}